/* stylelint-disable custom-property-empty-line-before */

/* stylelint-disable custom-property-empty-line-before */

.Button {
	composes: muller-14 from 'assets/styles/fonts.css';
	text-transform: uppercase;
	white-space: nowrap;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
	position: relative;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	border-radius: 3px;
	min-width: 50px;
	padding: 0 30px;
	border: 0;
}

.Button:disabled {
	cursor: not-allowed;
	opacity: 0.5;
}

.Button .Icon {
	margin-left: 10px;
}

.Spinner {
	position: absolute;
	right: 10px;
	top: initial;
	width: 14px;
}

.Primary {
	composes: Button;
	min-height: 42px;
	background-color: #9E3621;
	color: #FFF;
	-webkit-box-shadow: 0 15px 20px -15px rgba(158, 54, 33, 0.5);
	        box-shadow: 0 15px 20px -15px rgba(158, 54, 33, 0.5);
	fill: #FFF;
}

.Primary:hover {
	background: #DA322C;
	-webkit-box-shadow: 0 18px 20px -15px rgba(158, 54, 33, 0.5);
	        box-shadow: 0 18px 20px -15px rgba(158, 54, 33, 0.5);
}

.Primary:focus {
	background: #DA322C;
	-webkit-box-shadow: 0 18px 20px -15px rgba(158, 54, 33, 0.5);
	        box-shadow: 0 18px 20px -15px rgba(158, 54, 33, 0.5);
}

.Secondary {
	composes: Button;
}

.Secondary:hover {
	color: #B94530;
}

.Inline {
	composes: Button;
	fill: #FFF;
	color: #FFF;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.Inline:hover {
	color: #B94530;
}

.Outline {
	composes: Button;
	min-height: 42px;
	border: 2px solid white;
	color: white;
}

.Outline:hover {
	background: white;
	color: #9E3621;
}

.Outline:focus {
	background: white;
	color: #9E3621;
}

.SmallOutline {
	composes: Button;
	composes: muller-11-thin from 'assets/styles/fonts.css';
	text-transform: uppercase;
	height: 26px;
	background-color: transparent;
	max-width: 100%;
	padding: 0 9px;
	-ms-flex-direction: row-reverse;
	    flex-direction: row-reverse;
}

.SmallOutline .Spinner {
	margin-left: 5px;
	-ms-flex-order: -1;
	    order: -1;
	right: 0;
	height: auto;
	width: 10px;
	position: relative;
}

.SmallOutline .Icon {
	margin-left: 0;
	margin-right: 5px;
	width: 10px;
}

.SmallOutline:hover {
	border: 1px solid #9E3621;
	color: #FFF;
	fill: #FFF;
	background-color: #9E3621;
}

.SmallOutline:focus {
	border: 1px solid #9E3621;
	color: #FFF;
	fill: #FFF;
	background-color: #9E3621;
}

.SmallOutlineRed {
	composes: SmallOutline;
	border: 1px solid #9E3621;
	color: #9E3621;
	fill: #9E3621;
}

.SmallOutlineRed .Spinner {
	stroke: #9E3621;
}

.SmallOutline:hover .Spinner,
.SmallOutline:focus .Spinner {
	stroke: white;
}

.SmallOutline:hover .Spinner {
	stroke: #FFF;
}

.SmallOutline:hover {
	border: 1px solid #9E3621;
	color: #FFF;
	fill: #FFF;
	background-color: #9E3621;
}

.SmallOutlineRed:focus {
	border: 1px solid #9E3621;
	color: #FFF;
	fill: #FFF;
	background-color: #9E3621;
}

.SmallOutlineWhite {
	composes: SmallOutline;
	border: 1px solid white;
	color: white;
	fill: white;
}
