/* stylelint-disable custom-property-empty-line-before */

/* stylelint-disable-next-line block-no-empty */

.Container {
}

.Month {
	width: 100%;
}

.Week {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.Day {
	composes: muller-11 from 'assets/styles/fonts.css';
	-ms-flex: 1 1 auto;
	    flex: 1 1 auto;
	width: 50px;
	height: 25px;
	border-radius: 3px;
	cursor: pointer;
}

.Default {
	cursor: pointer;
}

.Selected {
	color: white;
	background-color: #9E3621;
}

.Inactive {
	opacity: 0.5;
}

.Weekday {
	font-weight: 400;
}

.Day.Default:hover {
	background-color: #DA322C;
	color: white;
}

.MonthsSelector {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	height: 30px;
}

.MonthsSelector button {
	-ms-flex: 1 0 auto;
	    flex: 1 0 auto;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	cursor: pointer;
}

.MonthsText {
	composes: lato-14 from 'assets/styles/fonts.css';
	width: 60%;
	text-align: center;
}

/* Year and Month list */

.PeriodContainer {
	min-height: 175px;
}

.Row {
	height: 43px;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
}

.ListItem {
	composes: muller-11 from 'assets/styles/fonts.css';
	-ms-flex: 1 1 33%;
	    flex: 1 1 33%;
	border-radius: 3px;
}

.ListItem:not(.Selected):hover {
	background-color: #DA322C;
	color: white;
}

.ListMonth,
.ListYear {
	composes: ListItem;
}
